.promo {
    margin-top: -128px;
    padding-top: 310px;
    padding-bottom: 260px;
    background-color: $color-default-black;
    background-image: $gray-to-bottom, url("../img/background/bg-promo.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    color: $color-alice-blue;

    @media (min-resolution: $retina-dppx) {
        background-image: $gray-to-bottom, url("../img/background/bg-promo@2x.jpg");
    }

    @include vp-1023 {
        margin-top: -100px;
        padding-top: 180px;
        padding-bottom: 186px;
        background-position: 20% 0;
    }

    @include vp-767 {
        padding-bottom: 169px;
        background-position: 26% 0;
    }
}

.promo h1 {
    margin: 0;
    margin-left: 125px;
    margin-bottom: 33px;
    font-family: $ff-fancy;
    font-size: 80px;
    font-weight: 700;
    line-height: 94px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    word-wrap: break-word;

    @include vp-1023 {
        margin-left: 0;
        margin-bottom: 0;
        font-size: 64px;
        line-height: 75px;
    }

    @include vp-767 {
        margin-bottom: 7px;
        font-size: 36px;
        line-height: 42px;
    }
}

.promo p {
    max-width: 650px;
    margin: 0;
    margin-left: 125px;
    margin-bottom: 47px;
    font-size: 22px;
    line-height: 34px;
    word-wrap: break-word;

    @include vp-1023 {
        margin-left: 0;
        margin-bottom: 56px;
    }

    @include vp-767 {
        font-size: 14px;
        line-height: 22px;
        margin-bottom: 44px;
    }
}

.promo a {
    margin-left: 125px;
    padding: 17px 27px;
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    letter-spacing: 0.05em;

    &:not(:last-child) {
        margin-right: 20px;
        margin-bottom: 20px;
    }

    @include vp-1023 {
        margin-left: 46px;
    }

    @include vp-767 {
        width: 100%;
        margin-left: 0;
        padding-top: 13px;
        padding-bottom: 13px;
        font-size: 14px;
        line-height: 16px;
    }
}

.promo a .short {
    display: none;

    @include vp-767 {
        display: block;
    }
}

.promo a .long {
    @include vp-767 {
        display: none;
    }
}